.vux-week-calendar {
  background-color: #fff;
}
.vux-week-calendar-day-item,
.vux-week-calendar-date-item {
  text-align: center;
}
.vux-week-calendar-day-item {
  color: #000;
}
.vux-week-calendar-each-day-box {
  padding: 5px 0;
}
.vux-week-calendar-each-day-box {
  position: relative;
}
.vux-week-calendar-each-day {
  position: relative;
  background-color: transparent;
  border: 1px solid transparent;
  display: inline-block;
  font-size: 16px;
  color: #444;
  width: 26px;
  height: 26px;
  line-height: 25px;
  text-align: center;
  border-radius: 50%;
  box-sizing: border-box;
}
.vux-week-calendar-is-selected .vux-week-calendar-each-day {
  background-color: #04BE02;
  color: #fff;
}
.vux-calendar-dot {
  display: block;
  text-align: center;
  width: 5px;
  height: 5px;
  position: absolute;
  left: 50%;
  bottom: 0px;
  margin-left: -2.5px;
  background-color: #f74c31;
  border-radius: 50%;
}
.vux-week-calendar-top-tip {
  position: absolute;
  left: -10px;
  top: 0;
  font-size: 20px;
  transform: scale(0.5);
  transform-origin: top left;
}
