




















@import '../../styles/weui/widget/weui-loading/weui-loading.less';
@import '../../styles/weui/widget/weui_tips/weui-loadmore.less';

.vux-loadmore{

  &.weui-loadmore_line{
    display: flex;
    border-top: 0;

    &:before, &:after{
      position: relative;
      top: -1px;
      flex: 1;
      content: '';
      border-top: 1px solid @weuiLineColorLight;
    }

  }
}
