.vux-fullpage-box {
  transform: translate3d(0, 0, 0);
}
.vux-fullpage-box:after {
  display: block;
  content: ' ';
  height: 0;
  clear: both;
}
.vux-fullpage-box.vux-fullpage-box-anim {
  transition: all 500ms ease-out 0s;
}
.vux-fullpage-item {
  display: block;
  overflow: hidden;
}
.vux-fullpage-dir-h {
  float: left;
}
