.weui-cell_radio > * {
  pointer-events: none;
}
.vux-radio-icon {
  width: 24px;
  height: 24px;
  display: inline-block;
  vertical-align: middle;
  margin-right: 5px;
}
.vux-radio-label {
  vertical-align: middle;
}
.weui-cells_radio.vux-radio-disabled .weui-check:checked + .weui-icon-checked:before {
  opacity: 0.5;
}
