[class^="weui-icon_"]:before,
[class*=" weui-icon_"]:before {
  margin: 0;
}
.weui-icon-success {
  font-size: 23px;
  color: #09BB07;
}
.weui-icon-waiting {
  font-size: 23px;
  color: #10AEFF;
}
.weui-icon-warn {
  font-size: 23px;
  color: #F43530;
}
.weui-icon-info {
  font-size: 23px;
  color: #10AEFF;
}
.weui-icon-success-circle {
  font-size: 23px;
  color: #09BB07;
}
.weui-icon-success-no-circle {
  font-size: 23px;
  color: #09BB07;
}
.weui-icon-waiting-circle {
  font-size: 23px;
  color: #10AEFF;
}
.weui-icon-circle {
  font-size: 23px;
  color: #C9C9C9;
}
.weui-icon-download {
  font-size: 23px;
  color: #09BB07;
}
.weui-icon-info-circle {
  font-size: 23px;
  color: #09BB07;
}
.weui-icon-safe-success {
  color: #09BB07;
}
.weui-icon-safe-warn {
  color: #FFBE00;
}
.weui-icon-cancel {
  color: #F43530;
  font-size: 22px;
}
.weui-icon-search {
  color: #B2B2B2;
  font-size: 14px;
}
.weui-icon-clear {
  color: #B2B2B2;
  font-size: 14px;
}
.weui-icon-delete.weui-icon_gallery-delete {
  color: #FFFFFF;
  font-size: 22px;
}
.weui-icon_msg {
  font-size: 93px;
}
.weui-icon_msg.weui-icon-warn {
  color: #F76260;
}
.weui-icon_msg-primary {
  font-size: 93px;
}
.weui-icon_msg-primary.weui-icon-warn {
  color: #FFBE00;
}
.weui-msg {
  padding-top: 36px;
  text-align: center;
}
.weui-msg__icon-area {
  margin-bottom: 30px;
}
.weui-msg__text-area {
  margin-bottom: 25px;
  padding: 0 20px;
}
.weui-msg__text-area a {
  color: #586C94;
}
.weui-msg__title {
  margin-bottom: 5px;
  font-weight: 400;
  font-size: 20px;
}
.weui-msg__desc {
  font-size: 14px;
  color: #999999;
}
.weui-msg__opr-area {
  margin-bottom: 25px;
}
.weui-msg__extra-area {
  margin-bottom: 15px;
  font-size: 14px;
  color: #999999;
}
.weui-msg__extra-area a {
  color: #586C94;
}
@media screen and (min-height: 438px) {
  .weui-msg__extra-area {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    text-align: center;
  }
}
.weui-btn {
  position: relative;
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding-left: 14px;
  padding-right: 14px;
  box-sizing: border-box;
  font-size: 18px;
  text-align: center;
  text-decoration: none;
  color: #FFFFFF;
  line-height: 2.33333333;
  border-radius: 5px;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  overflow: hidden;
}
.weui-btn:after {
  content: " ";
  width: 200%;
  height: 200%;
  position: absolute;
  top: 0;
  left: 0;
  border: 1px solid rgba(0, 0, 0, 0.2);
  transform: scale(0.5);
  transform-origin: 0 0;
  box-sizing: border-box;
  border-radius: 10px;
}
.weui-btn_inline {
  display: inline-block;
}
.weui-btn_default {
  color: #000000;
  background-color: #F8F8F8;
}
.weui-btn_default:not(.weui-btn_disabled):visited {
  color: #000000;
}
.weui-btn_default:not(.weui-btn_disabled):active {
  color: rgba(0, 0, 0, 0.6);
  background-color: #DEDEDE;
}
.weui-btn_primary {
  background-color: #1AAD19;
}
.weui-btn_primary:not(.weui-btn_disabled):visited {
  color: #FFFFFF;
}
.weui-btn_primary:not(.weui-btn_disabled):active {
  color: rgba(255, 255, 255, 0.6);
  background-color: #179B16;
}
.weui-btn_warn {
  background-color: #E64340;
}
.weui-btn_warn:not(.weui-btn_disabled):visited {
  color: #FFFFFF;
}
.weui-btn_warn:not(.weui-btn_disabled):active {
  color: rgba(255, 255, 255, 0.6);
  background-color: #CE3C39;
}
.weui-btn_disabled {
  color: rgba(255, 255, 255, 0.6);
}
.weui-btn_disabled.weui-btn_default {
  color: rgba(0, 0, 0, 0.3);
  background-color: #F7F7F7;
}
.weui-btn_disabled.weui-btn_primary {
  background-color: #9ED99D;
}
.weui-btn_disabled.weui-btn_warn {
  background-color: #EC8B89;
}
.weui-btn_loading .weui-loading {
  margin: -0.2em 0.34em 0 0;
}
.weui-btn_loading.weui-btn_primary,
.weui-btn_loading.weui-btn_warn {
  color: rgba(255, 255, 255, 0.6);
}
.weui-btn_loading.weui-btn_primary .weui-loading,
.weui-btn_loading.weui-btn_warn .weui-loading {
  background-image: url("data:image/svg+xml;base64,");
}
.weui-btn_loading.weui-btn_primary {
  background-color: #179B16;
}
.weui-btn_loading.weui-btn_warn {
  background-color: #CE3C39;
}
.weui-btn_plain-primary {
  color: #1aad19;
  border: 1px solid #1aad19;
}
.weui-btn_plain-primary:not(.weui-btn_plain-disabled):active {
  color: rgba(26, 173, 25, 0.6);
  border-color: rgba(26, 173, 25, 0.6);
  background-color: transparent;
}
.weui-btn_plain-primary:after {
  border-width: 0;
}
.weui-btn_plain-default {
  color: #353535;
  border: 1px solid #353535;
}
.weui-btn_plain-default:not(.weui-btn_plain-disabled):active {
  color: rgba(53, 53, 53, 0.6);
  border-color: rgba(53, 53, 53, 0.6);
}
.weui-btn_plain-default:after {
  border-width: 0;
}
/*
* added by VUX
*/
button.weui-btn.weui-btn_plain-warn,
input.weui-btn.weui-btn_plain-warn {
  border-width: 1px;
  background-color: transparent;
}
.weui-btn_plain-warn {
  color: #ce3c39;
  border: 1px solid #ce3c39;
  background: transparent;
}
.weui-btn_plain-warn:not(.weui-btn_plain-disabled):active {
  color: rgba(206, 60, 57, 0.6);
  border-color: rgba(206, 60, 57, 0.6);
}
.weui-btn_plain-warn:after {
  border-width: 0;
}
.weui-btn_plain-disabled {
  color: rgba(0, 0, 0, 0.2);
  border-color: rgba(0, 0, 0, 0.2);
}
button.weui-btn,
input.weui-btn {
  width: 100%;
  border-width: 0;
  outline: 0;
  -webkit-appearance: none;
}
button.weui-btn:focus,
input.weui-btn:focus {
  outline: 0;
}
button.weui-btn_inline,
input.weui-btn_inline,
button.weui-btn_mini,
input.weui-btn_mini {
  width: auto;
}
button.weui-btn_plain-primary,
input.weui-btn_plain-primary,
button.weui-btn_plain-default,
input.weui-btn_plain-default {
  border-width: 1px;
  background-color: transparent;
}
.weui-btn_mini {
  display: inline-block;
  padding: 0 1.32em;
  line-height: 2.3;
  font-size: 13px;
}
/*gap between btn*/
.weui-btn + .weui-btn {
  margin-top: 15px;
}
.weui-btn.weui-btn_inline + .weui-btn.weui-btn_inline {
  margin-top: auto;
  margin-left: 15px;
}
.weui-btn-area {
  margin: 1.17647059em 15px 0.3em;
}
.weui-btn-area_inline {
  display: flex;
}
.weui-btn-area_inline .weui-btn {
  margin-top: auto;
  margin-right: 15px;
  width: 100%;
  flex: 1;
}
.weui-btn-area_inline .weui-btn:last-child {
  margin-right: 0;
}
